.section-feedback {
    padding: 3.75rem 0;
}

.feedback__headline {
    text-align: center;
    color: var(--neutrals-900);
    margin-bottom: 2rem;
}

.feedback__description {
    width: 70%;
    text-align: center;
    margin: 0 auto;
    color: var(--neutrals-300);
    font-weight: 600;
    text-wrap: balance;
}

.feedback__wrap {
    margin-top: 3.25rem;
    .notification {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 1.5rem;
        pointer-events: none;
        transition: var(--transition-duration);
        opacity: 0;
        &.show {
            opacity: 1;
        }
    }
}

.feedback__form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 1.25rem; 
    opacity: 1;
    transition: var(--transition-duration);
    
    .input__container {
        position: relative;
        display: flex;
        gap: 0.75rem;
        align-items: center;
        svg {
            position: absolute;
            left: 1.125rem;
        }
        input {
            padding: 1.125rem;
            padding-left: 3.625rem;
            width: 18.5rem;
            height: 3.75rem;
            border: 2px solid;
            border-color: var(--primary-200);
            border-radius: 0.75rem;
            font-family: var(--main-font-family);
            font-style: normal;
            font-size: 1rem;
            transition: var(--transition-duration);
            &::placeholder {
                color: var(--neutrals-300)
            }
        }
    }
    .empty-field {
        border-color: red;
    }
    &.hidden {
        opacity: 0;
    }
}

@include respond-to(bigMobile) {
    .feedback__wrap {
        margin-top: 1.95rem;
    }
    .feedback__form {
        flex-direction: column;
    }
}